.navbar{
    display: flex;
    box-shadow: rgba(0, 0, 0, .15) 0px -2px 10px 0px;
    justify-content: space-between;
    align-items: center;
    padding: .5em 2em;
    
}

.brand{
    text-decoration: none;
    font-size: 1.2em;
}

.brand span{
    font-weight: 800;
    text-transform: uppercase;
}

.links_list{
    display: flex;
    list-style: none;
}

.links_list li{
    margin-right: 1em;
}

.links_list li a{
    padding: .4em .6em;
    text-decoration: none;
    border-radius: 5px;
}

.active{
    background-color: var(--preto);
    color: var(--lilas);
}